import React from 'react';
import './ReturnArray.css';
import DragonBase from './../../components/Tables/DragonBase';

const headers = ['ID', '', '', '','','','','',' ',' '];

const rows = [
  ['1', '', '208', '10','','','12','4','1400','157'],
  ['2', '', '181', '18','','','12','4','1400','157'],
  ['3', '','181', '18','','','12','4','1400','157'],
  ['4', '','181', '18','','','12','4','1400','157'],
  ['5', '','181', '18','','','12','4','1400','157'],
];

function ReturnArray(props)
{
  return (
    <div>
    <header><h1>  {props.namesubstring}:</h1></header>
    <main>
    <DragonBase rows={rows} headers={headers} rowHeaders page="1     " />
    </main>
    </div>
  );
}

export default ReturnArray;