import React from 'react';
import './HomePage.css';
import DragonBase from './../../components/Tables/DragonBase'
import HeaderButton from './../../components/Buttons/HeaderButton';
import BaseInput from './../../components/Inputs/BaseInput';
import filterFactory, { textFilter,selectFilter, numberFilter } from 'react-bootstrap-table2-filter';
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Link,
  useRouteMatch,
  useParams,
} from 'react-router-dom';

const selectTypes = {
  WATER: 'WATER',
  UNDERGROUND:'UNDERGROUND',
  AIR:'AIR',
  FIRE:'FIRE'
};

const selectCharacters = {
  CUNNING: 'CUNNING',
  WISE: 'WISE',
  CHAOTIC: 'CHAOTIC',
  CHAOTIC_EVIL: 'CHAOTIC_EVIL',
  FICKLE: 'FICKLE'
};

const headers = [{
  dataField: 'id',
  text:'ID',
  sort: true,
  filter: numberFilter()},{
  dataField:'name',
  text:'',
  sort: true,
  filter: textFilter()},{
  dataField:'age',
  text:'',
  sort: true,
  filter: numberFilter()},{
  dataField:'weight',
  text:'',
  sort: true,
  filter: numberFilter()},{
  dataField:'character',
  text:'',
  sort: true,
  filter: selectFilter({
    options: selectCharacters
  })},{
  dataField:'type',
  text:'',
  sort: true,
  filter: selectFilter({
    options: selectTypes
  })},{
  dataField:'x',
  text:'',
  sort: true,
  filter: numberFilter()},{
  dataField:'y',
  text:'',
  sort: true,
  filter: numberFilter()},{
  dataField:'cave',
  text:' ',
  sort: true,
  filter: numberFilter()},{
  dataField:'treasure',
  text:' ',
  sort: true,
  filter: numberFilter()}
];


const rows = [
{id:'1',name: '',age: '208',weight: '10',character:'CHAOTIC_EVIL',type:'FIRE',x:'12',y:'4',cave:'1400',treasure:'157'},
{id:'2',name: '',age: '181',weight: '18',character:'FICKLE',type:'WATER',x:'12',y:'4',cave:'1400',treasure:'157'},
{id:'3',name: '',age:'181',weight: '18',character:'CUNNING',type:'FIRE',x:'12',y:'4',cave:'1400',treasure:'157'},
{id:'4',name: '',age:'181',weight: '18',character:'WISE',type:'AIR',x:'12',y:'4',cave:'1400',treasure:'157'},
{id:'5',name: '',age:'181',weight: '18',character:'CHAOTIC',type:'WATER',x:'12',y:'4',cave:'1400',treasure:'157'}
];

function HomePage()
{
  return (
    <div>
    <main>
    <DragonBase rows={rows} headers={headers} rowHeaders page="1     " />
    </main>
    <footer>
    <ul className='NeededOperations'>
    <li>
    <BaseInput 
    name=" ,    :" 
    enter="  "
    />
    </li>
    <li><Link to="/returnarray"><HeaderButton name=""/></Link></li>
    </ul>
    <ul className='NeededOperations'> 
    <li>
    <BaseInput 
    name=" ,   :" 
    enter=" "
    />
    </li>
    <li><Link to='/charactercount'> <HeaderButton name=""/></Link> </li>
    </ul>
    <ul className='NeededOperations'>
    <li>
    <BaseInput 
    name=" ,  :" 
    enter=" "
    />
    </li>
    <li><Link to='/typecount'> <HeaderButton name=""/> </Link></li>
    </ul> 
    </footer>
    </div>
  );
}

export default HomePage;
